.tjs-app{max-height:100%;background:url(/ui/denim075.png) repeat;border-radius:5px;box-shadow:0 0 20px #000;margin:3px 0;color:#f0f0e0;position:absolute}.tjs-window-app{display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:flex-start;padding:0;z-index:99}.tjs-window-app .window-content{display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:flex-start;padding:8px;color:#191813;overflow-y:auto;overflow-x:hidden}.tjs-window-app .window-header{flex:0 0 30px;overflow:hidden;padding:0 8px;line-height:30px;border-bottom:1px solid #000;pointer-events:auto}.tjs-window-app .window-header a{flex:none;margin:0 0 0 8px}.tjs-window-app .window-header h4{font-family:Signika, sans-serif}.tjs-window-app .window-header i[class^=fa]{margin-right:3px}.tjs-window-app .window-header .window-title{margin:0;word-break:break-all}.tjs-window-app .window-resizable-handle{width:20px;height:20px;position:absolute;bottom:-1px;right:0;background:#444;padding:2px;border:1px solid #111;border-radius:4px 0 0 0}.tjs-window-app .window-resizable-handle i.fas{transform:rotate(45deg)}.tjs-window-app.minimized .window-header{border:1px solid #000}